.App {
  text-align: start;
  background-color: #202124;
  min-height: 100vh;
  min-width: 100vw;
  justify-content: center;
  font-size: calc(32px + 6vmin);
  color: white;
}

.pageList {
  position: fixed;
  display: flex;
  flex-direction: column;
  width: 28vw;
  top: 50%; /* Position vertically in the center */
  transform: translateY(-50%); /* Center vertically */
}

.selButton {
  font-size: 6vw;
  border: none;
  color: rgb(167, 167, 167);
  background-color: #262731;
  margin: 2.5%;
  margin-left: 0px;
}

.returnHomeButton {
  position: fixed;
  font-size: calc(6vw);
}